// main: ../style.scss
@import '../global/vars';
@import '../global/mixin';

.module-stripe {
  .wrapper-padded {
    overflow-y: hidden;
  }

  .stripe-listed {
    margin-bottom: $flex-hold-column-wide-space-desktop;

    &:last-child {
      margin-bottom: 0;
    }

    &.stripe-image-text {
      .module-stripe-image {
        order: 1;

        .spacer {
          padding-right: $flex-hold-column-thin-space-desktop;
        }
      }

      .module-stripe-text {
        order: 2;

        .spacer {
          padding: 20px $flex-hold-column-thin-space-desktop 20px $flex-hold-column-wide-space-desktop;
          max-width: 850px;
        }
      }
    }

    &.stripe-text-image {
      .module-stripe-image {
        order: 2;

        .spacer {
          padding-left: $flex-hold-column-thin-space-desktop;
        }
      }

      .module-stripe-text {
        order: 1;

        .spacer {
          padding: 20px $flex-hold-column-wide-space-desktop 20px $flex-hold-column-thin-space-desktop;
          max-width: 850px;
          margin-left: auto;
        }
      }
    }

    .cta-holder {
      text-align: inherit;
      margin: 32px 0 0;
    }
  }

  .cta-holder {
    text-align: center;
    margin: $module-vertical-padding-desktop 0 0;
  }
}